<template>
  <q-layout view="hHh Lpr lFf">
    <q-layout-header>
      <q-toolbar
        color="secondary"
       
      >
        <q-btn
          flat
          dense
          round
          @click="leftDrawerOpen = !leftDrawerOpen"
          aria-label="Menu"
        >
          <q-icon name="menu" />
        </q-btn>

        <q-toolbar-title>
          GTiBE portal
          <div slot="subtitle">v{{ $q.version }}</div>
        </q-toolbar-title>
      </q-toolbar>
    </q-layout-header>

    <q-layout-drawer
      v-model="leftDrawerOpen"
      :content-class="$q.theme === 'mat' ? 'bg-grey-2' : null"
    >
      <q-list
        no-border
        link
        inset-delimiter
      >
      


    <q-list-header>Разделы портала</q-list-header>
      <q-item>
        <router-link to="/case">Кейсы</router-link>
      </q-item>        
      <q-item>
        <router-link to="/gemof">Геймофикация</router-link>
      </q-item>

      <q-item>
        <router-link to="">Пройти курс</router-link>
      </q-item>

      <q-item>
        <router-link to="">Входное тестирование</router-link>
      </q-item>

    </q-list>
     
    </q-layout-drawer>

    <q-page-container>
      <router-view />
    </q-page-container>
  </q-layout>
</template>

<script>
import { openURL } from 'quasar'

export default {
  name: 'LayoutDefault',
  data () {
    return {
      leftDrawerOpen: this.$q.platform.is.desktop
    }
  },
  methods: {
    openURL
  }
}
</script>

<style>
</style>



